import React from 'react'
import Helmet from 'react-helmet'
import PropTypes from 'prop-types'
import Content from '../Content'

const AboutPageTemplate = ({ meta_title, meta_description, title, team, content, contentComponent }) => {
  const PageContent = contentComponent || Content

  return (
    <div className='flex flex-col bg-white'>
      <div className='container px-8 mx-auto'>
        <Helmet>
          <title>{meta_title}</title>
          <meta name='description' content={meta_description} />
        </Helmet>

        <main className='flex flex-wrap items-center py-12 my-12 text-center md:text-left lg:px-24'>
          <div className='w-full whitespace-pre-line md:w-full'>
            <h1 className='mb-6 text-5xl leading-tight text-black'>
              {title}
            </h1>
          </div>
        </main>

        <PageContent className='mb-12 markdown' content={content} />

        {/* Team */}
        <h2 className='text-lg'>Team</h2>
        <div className='flex flex-wrap'>
          {team.map(({ name, img, short_description }) => (
            <div key={name} className='w-full md:w-1/3 px-4'>
              <div key={name} className='overflow-hidden rounded shadow-lg'>
                <img className='w-full' src={img} alt='Sunset in the mountains' />
                <div className='px-6 py-4'>
                  <div className='mb-2 text-xl font-bold'>{name}</div>
                  <p className='text-base text-gray-700'>
                    {short_description}
                  </p>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  )
}

AboutPageTemplate.propTypes = {
  title: PropTypes.string.isRequired,
  content: PropTypes.string,
  contentComponent: PropTypes.func,
}

export default AboutPageTemplate
